<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="container mt-5">
      <div class="row justify-content-center"><img class="icon-img img-fluid" src="@/assets/icon.svg" alt=""/></div>
      <div class="row justify-content-center py-2"><h1></h1></div>
    </div>
    <!-- formsubmitsigninfunction -->
    <form class="form-signin text-center mt-3" @submit.prevent="signin">
      <h2 class="h3 mb-3 font-weight-normal"></h2>
      <label for="inputEmail" class="sr-only">Email address</label>
      <!-- inputv-modeldatadatauserusername -->
      <input
        type="email"
        id="inputEmail"
        class="form-control"
        placeholder="E-mail"
        v-model="user.username"
        required
        autofocus
      />
      <label for="inputPassword" class="sr-only">Password</label>
      <!-- inputv-modeldatadatauserpassword -->
      <input
        type="password"
        id="inputPassword"
        class="form-control"
        placeholder=""
        v-model="user.password"
        required
      />
      <div class="checkbox mb-3">
        <label>
          <input type="checkbox" value="remember-me" /> 
        </label>
      </div>
      <button class="btn btn-lg btn-primary btn-block" type="submit"></button>
      <p class="mt-5 mb-3 text-muted">&copy; Copyright 2020</p>
    </form>
  </div>
</template>

<script>
import {mapActions,mapGetters} from 'vuex';
export default {
  name: "HelloWorld",
  data() {
    return {
      //userusernamepassword
      user: {
        username: "",
        password: ""
      }
    };
  },
  computed:{
    ...mapGetters(['isLoading'])
  },
  //methodsloginfunction
  methods: {
    signin() {
      //functionaxios
      //signinAPI/signin
      //API/admin/signincookiemain.jsaxiosaxios.defaults.withCredentials=true)
      const api = `${process.env.APIPATH}/admin/signin`;
      const vm = this;
      vm.$store.dispatch('updateLoading',true)
      //post
      this.$http.post(api,vm.user).then(response => {
        // console.log(response.data);
        //router
        if(response.data.success){
          vm.$store.dispatch('updateLoading',false)
            vm.$router.push('/admin/products')
        }
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
html,
body {
  height: 100%;
  background-color: #f5f5f5;
}

body {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  justify-content: center;
  padding-top: 40px;
  padding-bottom: 40px;
  
}

.form-signin {
  width: 100%;
  max-width: 330px;
  padding: 15px;
  margin: auto;
}
.form-signin .checkbox {
  font-weight: 400;
}
.form-signin .form-control {
  position: relative;
  box-sizing: border-box;
  height: auto;
  padding: 10px;
  font-size: 16px;
}
.form-signin .form-control:focus {
  z-index: 2;
}
.form-signin input[type="email"] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
.form-signin input[type="password"] {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
.icon-img{
  max-width: 120px;
  max-height: 120px;
}
</style>



