<template>
  <div>
    <!-- bs4paginationpagespaginationpages -->
    <nav aria-label="Page navigation">
      <!-- d-flexjustify-content-center -->
      <ul class="pagination d-flex justify-content-center">
        <!-- paginationhas_prefalsedisabledclass
        current_page-1getProducts-->
        <li class="page-item" :class="{'disabled':!pages.has_pre}">
          <a
            class="page-link"
            href="#"
            aria-label="Previous"
            @click.prevent="updatePage(pages.current_page-1)"
          >
            <span aria-hidden="true">&laquo;</span>
          </a>
        </li>
        <!-- v-for
        datapaginationtotal_pages22page
        classcurrent_pagepageactiveclass-->
        <li
          class="page-item"
          v-for="page in pages.total_pages"
          :key="page"
          :class="{'active':pages.current_page==page}"
        >
          <!-- {{page}}updatePagefunctiontemitPagesgetProducts -->
          <a class="page-link" href="#" @click.prevent="updatePage(page)">{{page}}</a>
        </li>
        <li class="page-item" :class="{'disabled':!pages.has_next}">
          <a
            class="page-link"
            href="#"
            aria-label="Next"
            @click.prevent="updatePage(pages.current_page+1)"
          >
            <span aria-hidden="true">&raquo;</span>
          </a>
        </li>
      </ul>
    </nav>
  </div>
</template>

<script>
export default {
    //propspagesProductspagination
    props:['pages'],
    data(){
        return{
            
        }
    },
    methods:{
        //(page)
        updatePage(page){
            this.$emit('emitPages',page)
        },
    },
};
</script>


