// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
//axiosvue-axiosimportentry
import axios from 'axios';
import VueAxios from 'vue-axios';
import Vuex from 'vuex';  //vuex
import store from './store'  //vuexstoremain.js

//vue-loading-overlayloadingnpm install vue-loading-overlay
//main.jsimportcssimport
import Loading from 'vue-loading-overlay';  //component
import 'vue-loading-overlay/dist/vue-loading.css';

//bootstrap import
import 'bootstrap';

//vee-validate
import {ValidationProvider,extend,ValidationObserver} from 'vee-validate';
//
import {required,email} from 'vee-validate/dist/rules'
//
import {localize} from 'vee-validate';
import TW from 'vee-validate/dist/locale/zh_TW.json'

import App from './App';
import router from './router';

//eventbusbus.jsimport
import './bus';
//src/filterscurrencyFilter.js
import currencyFilter from './filters/currencyFilter';
import dateFilter from './filters/dateFilter';


Vue.config.productionTip = false
//import axiosvue-axiosVueAxiosaxios
Vue.use(VueAxios, axios);
//apiaxios
axios.defaults.withCredentials = true;

//Vuex
Vue.use(Vuex);

//Componentvue-loading-overlay
Vue.component('Loading',Loading);   //templateloading

//importValidationProviderComponent
Vue.component('ValidationProvider',ValidationProvider)
//
extend('email',email);
extend('required',{
  ...required,
message:'{_field_}'})
//
localize('zh_TW',TW);
//ValidationObserver
Vue.component('ValidationObserver',ValidationObserver);

//filtercurrencyFilterfiltercurrencyfilter
Vue.filter('currency',currencyFilter);
Vue.filter('date',dateFilter);

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,    //VuexstoreVue
  components: { App },
  template: '<App/>'
});

//Vuerouter.beforeEach
//functiontofromnext
router.beforeEach(function (to, from, next) {
  // console.log('to', to, 'from', from, 'next', next)
  //ifto(router/index.js)metarequiresAuth=true
  if (to.meta.requiresAuth) {
    // console.log('')
    // API
    const api = `${process.env.APIPATH}/api/user/check`;
      //post
      //routervuethis.$httppostaxiospost
      axios.post(api).then(response => {
        // console.log(response.data);
        //response.data.success==truenext()
        if(response.data.success){
            next();
        }else{
          //nextpath
          next({
            path:'/login',
          });
          alert(response.data.message)
        }
      });
  } else {
    //meta.requiresAuth
    next();
  }
})



