<template>
  <div>
    <!-- <Loading :active.sync="isLoading"></Loading> -->
    <div class="text-right mt-3">
      <button class="btn btn-primary" @click="openCouponModal(true)"></button>
    </div>
    <div class="row d-flex justify-content-center">
      <table class="table col-10 mt-4">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, key) in coupons" :key="key">
            <td>{{ item.title }}</td>
            <td>{{item.code}}</td>
            <td>{{ item.percent }}%</td>
            <td>{{ item.due_date | date }}</td>
            <td>
              <span v-if="item.is_enabled === 1" class="text-success"></span>
              <span v-else class="text-muted"></span>
            </td>
            <td>
              <button
                class="btn btn-outline-primary btn-sm"
                @click="openCouponModal(false, item)"
              ></button>
              <button class="btn btn-outline-danger btn-sm" @click="delCouponModal(item)"></button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <!-- CouponModal -->
    <div
      class="modal fade"
      id="couponModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="modalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header bg-primary text-white">
            <h5 class="modal-title" id="exampleModalLabel" v-if="isNew"></h5>
            <h5 class="modal-title" id="exampleModalLabel" v-else></h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <label for="title"></label>
              <input
                type="text"
                class="form-control"
                id="title"
                v-model="tempCoupon.title"
                placeholder=""
              />
            </div>
            <div class="form-group">
              <label for="coupon_code"></label>
              <input
                type="text"
                class="form-control"
                id="coupon_code"
                v-model="tempCoupon.code"
                placeholder=""
              />
            </div>
            <div class="form-group">
              <label for="due_date"></label>
              <input type="date" class="form-control" id="due_date" v-model="due_date" />
            </div>
            <div class="form-group">
              <label for="price">%</label>
              <input
                type="number"
                class="form-control"
                id="price"
                v-model="tempCoupon.percent"
                placeholder=""
              />
            </div>
            <div class="form-group">
              <div class="form-check">
                <input
                  class="form-check-input"
                  type="checkbox"
                  :true-value="1"
                  :false-value="0"
                  v-model="tempCoupon.is_enabled"
                  id="is_enabled"
                />
                <label class="form-check-label" for="is_enabled"></label>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary" @click="updateCoupon">
              <span v-if="isNew"></span>
              <span v-else></span>
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- Modal for Coupon -->
    <div
      class="modal fade"
      id="delCouponModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span>Coupon</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            
            <strong class="text-danger">{{ tempCoupon.title }}</strong> ()
            <div>
              <strong></strong>
            </div>
            <div>{{tempCoupon.title}}</div>
            <div>{{tempCoupon.code}}</div>
            <div>{{tempCoupon.percent}}%</div>
            <div>{{tempCoupon.due_date|date}}</div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger" @click="delCoupon"></button>
          </div>
        </div>
      </div>
    </div>

    <!-- paginationpaginationpropspages
    functionemitPagesgetOrders-->
    <Pagination :pages="pagination" @emitPages="getCoupons" v-if="pagination.total_pages>0"></Pagination>
  </div>
</template>

<script>
import $ from "jquery";
//
import Pagination from "../Pagination";

export default {
  components: {
    Pagination
  },
  data() {
    return {
      // isLoading: false, //loading
      pagination: "", //
      coupons: {}, //coupons
      tempCoupon: {
        //coupon
        title: "",
        is_enabled: 0,
        percent: 100,
        due_date: 0,
        code: ""
      },
      isNew: false, //coupon
      due_date: new Date()
    };
  },
  watch: {
    due_date() {
      const vm = this;
      const timestamp = Math.floor(new Date(vm.due_date) / 1000);
      vm.tempCoupon.due_date = timestamp;
    }
  },
  methods: {
    //coupons
    getCoupons(page = 1) {
      //axiospage1pagination
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupons?page=${page}`;
      // vm.isLoading = true; //loading
      vm.$store.dispatch('updateLoading',true)
      this.$http.get(api).then(response => {
        console.log(response.data);
        vm.coupons = response.data.coupons; //couponcoupons
        vm.pagination = response.data.pagination; //paginationpagination
        // vm.isLoading = false; //loading
        vm.$store.dispatch('updateLoading',false)
      });
      console.log("getCoupons");
    },
    //couponisNew=true
    openCouponModal(isNew, item) {
      const vm = this;
      vm.isNew = isNew;
      vm.tempCoupon = Object.assign({}, item);
      $("#couponModal").modal("show");
    },
    //coupontempCoupon
    updateCoupon() {
      const vm = this;
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon`;
      let httpMethod = "post";
      if (!vm.isNew) {
        api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
        httpMethod = "put";
      }
      // vm.isLoading = true; //loading
      vm.$store.dispatch('updateLoading',true)
      this.$http[httpMethod](api, { data: vm.tempCoupon }).then(response => {
        console.log(response.data);
        if (response.data.success) {
          vm.getCoupons(); //
          // vm.isLoading = false; //loading
          vm.$store.dispatch('updateLoading',false)
          $("#couponModal").modal("hide"); //Modal
          // //evenbusAlertMessageAlert
          // vm.$bus.$emit("message:push", response.data.message, "success");
          //vuexalertMessage
          vm.$store.dispatch("updateAlertMessage", {
            message: response.data.message,
            status: "success"
          });
        } else {
          // //evenbusAlertMessageAlert
          // vm.$bus.$emit("message:push", response.data.message, "danger");
          //vuexalertMessage
          vm.$store.dispatch("updateAlertMessage", {
            message: response.data.message,
            status: "danger"
          });
        }
      });
      console.log("updateCoupon");
    },
    //Modal
    delCouponModal(item) {
      //tempCoupon
      this.tempCoupon = Object.assign({}, item);
      $("#delCouponModal").modal("show"); //Modal
    },
    //Coupon
    delCoupon() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
      // vm.isLoading = true; //loading
      vm.$store.dispatch('updateLoading',true)
      this.$http.delete(api).then(response => {
        console.log(response.data);
        vm.getCoupons(); //coupons
        // vm.isLoading = false; //loading
        vm.$store.dispatch('updateLoading',false)
        $("#delCouponModal").modal("hide"); //Modal
        if (response.data.success) {
          // //evenbusAlertMessageAlert
          // vm.$bus.$emit("message:push", response.data.message, "success");
          vm.$store.dispatch("updateAlertMessage", {
            message: response.data.message,
            status: "success"
          });
        } else {
          // vm.$bus.$emit("message:push", response.data.message, "danger");
          vm.$store.dispatch("updateAlertMessage", {
            message: response.data.message,
            status: "danger"
          });
        }
      });
    }
  },
  created() {
    this.getCoupons();
  }
};
</script>


