<template>
  <div>
    <!-- loadingvue-loading-overlayisLoadingdata -->
    <loading :active.sync="isLoading"></loading>
    <ClientNavbar ref="clientnavbar"></ClientNavbar>
    <Alert></Alert>
    <div class="container mt-4">
      <div class="row">
        <div class="col">
          <button class="btn btn-light" @click="goBack">
            <i class="fas fa-2x fa-reply"></i>
          </button>
        </div>
      </div>
      <div class="row justify-content-center align-items-center my-5">
        <div class="col-md-8">
          <!--  -->
          <div class="row justify-content-center">
            <h1 class="text-secondary"> </h1>
          </div>
          <!--  -->
          <div class="row justify-content-around mb-3">
            <div
              class="order-status col-3 d-flex justify-content-center align-items-center py-1"
              :class="{'active':status=='cart'}"
            >
              <span></span>
            </div>
            <div
              class="order-status col-3 d-flex justify-content-center align-items-center py-1"
              :class="{'active':status=='userdata'}"
            >
              <span></span>
            </div>
            <div
              class="order-status col-3 d-flex justify-content-center align-items-center py-1"
              :class="{'active':status=='checkout'}"
            >
              <span></span>
            </div>
          </div>
          <!--  -->
          <div class="row" v-if="status!=='checkout'">
            <div class="card w-100">
              <div
                class="card-header"
                data-toggle="collapse"
                data-target="#collapseCart"
                aria-expanded="true"
                aria-controls="collapseCart"
                id="headingOne"
              >
                <div class="row px-3 justify-content-between align-items-center">
                  <h4 class="mb-0"></h4>
                  <h5 class="mb-0">{{cart.final_total}}</h5>
                </div>
              </div>

              <div
                id="collapseCart"
                class="collapse"
                :class="{'show':status=='cart'}"
                aria-labelledby="headingOne"
              >
                <div class="card-body">
                  <table class="table mb-0">
                    <thead>
                      <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                      </tr>
                    </thead>
                    <tbody>
                      <!-- datacartcarts -->
                      <tr v-for="item in cart.carts" :v-if="cart.carts" :key="item.id">
                        <td class="align-middle">
                          <!-- removeCartItemid -->
                          <button
                            class="btn btn-outline-danger btn-sm"
                            @click="removeCartItem(item.id)"
                          >
                            <i class="far fa-trash-alt"></i>
                          </button>
                        </td>
                        <td class="align-middle">
                          {{item.product.title}}
                          <div class="badge badge-success" v-if="item.coupon"></div>
                        </td>
                        <td class="align-middle">{{item.qty}}/{{item.product.unit}}</td>
                        <td class="align-middle text-right">{{item.final_total}}</td>
                      </tr>
                    </tbody>
                    <tfoot>
                      <tr>
                        <td colspan="3" class="text-right"></td>
                        <!-- datacarttotal -->
                        <td class="text-right">{{cart.total}}</td>
                      </tr>
                      <tr v-if="cart.final_total!==cart.total">
                        <td colspan="3" class="text-right text-success"></td>
                        <td class="text-right">{{cart.final_total}}</td>
                      </tr>
                    </tfoot>
                  </table>
                </div>
              </div>
            </div>
          </div>
          <!--  -->
          <div v-if="status=='cart'">
            <div class="row">
              <div class="input-group mb-3 input-group-sm mt-3">
                <!-- inputdatacoupon_code -->
                <input type="text" class="form-control" placeholder="" v-model="coupon_code" />
                <div class="input-group-append">
                  <!-- addCouponCode -->
                  <button
                    class="btn btn-outline-primary"
                    type="button"
                    @click="addCouponCode()"
                  ></button>
                </div>
              </div>
            </div>
            <div class="row justify-content-end">
              <button class="btn btn-primary" @click="changeStatus('userdata')"></button>
            </div>
          </div>
          <!--  -->
          <div v-if="status=='userdata'">
            <div class="row justify-content-center my-3">
              <h4></h4>
            </div>
            <ValidationObserver v-slot="{handleSubmit}">
              <!-- formsubmitcreateOrdersubmit -->
              <form @submit.prevent="handleSubmit(createOrder)">
                <div class="form-group">
                  <label for="useremail">Email</label>
                  <ValidationProvider name="Email" rules="required|email" v-slot="{errors}">
                    <input
                      type="text"
                      class="form-control"
                      name="email"
                      id="useremail"
                      v-model="form.user.email"
                      placeholder=" Email"
                    />
                    <span class="text-danger">{{errors[0]}}</span>
                  </ValidationProvider>
                </div>
                <div class="form-group">
                  <label for="username"></label>
                  <ValidationProvider name="" rules="required" v-slot="{errors}">
                    <input
                      type="text"
                      class="form-control"
                      name="name"
                      id="username"
                      v-model="form.user.name"
                      placeholder=""
                    />
                    <span class="text-danger">{{errors[0]}}</span>
                  </ValidationProvider>
                </div>

                <div class="form-group">
                  <label for="usertel"></label>
                  <input
                    type="tel"
                    class="form-control"
                    id="usertel"
                    v-model="form.user.tel"
                    placeholder=""
                  />
                </div>

                <div class="form-group">
                  <label for="useraddress"></label>
                  <ValidationProvider name="" rules="required" v-slot="{errors}">
                    <input
                      type="text"
                      class="form-control"
                      name="address"
                      id="useraddress"
                      v-model="form.user.address"
                      placeholder=""
                    />
                    <span class="text-danger">{{errors[0]}}</span>
                  </ValidationProvider>
                </div>

                <div class="form-group">
                  <label for="comment"></label>
                  <textarea
                    name
                    id="comment"
                    class="form-control"
                    cols="30"
                    rows="5"
                    v-model="form.message"
                  ></textarea>
                </div>
                <div class="row justify-content-between px-3">
                  <button class="btn btn-light" @click="changeStatus('cart')"></button>
                  <button type="submit" class="btn btn-danger"></button>
                </div>
              </form>
            </ValidationObserver>
          </div>
          <!--  -->
          <div v-if="status=='checkout'">
            <div class="row justify-content-center my-3">
              <h4></h4>
            </div>
            <div class="row justify-content-center">
              <!-- formsubmitpayOrder -->
              <form class="col" @submit.prevent="payOrder">
                <table class="table">
                  <thead>
                    <th></th>
                    <th></th>
                    <th></th>
                  </thead>
                  <tbody>
                    <!-- dataorderproducts -->
                    <tr v-for="item in order.products" :key="item.id">
                      <td class="align-middle">{{ item.product.title }}</td>
                      <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
                      <td class="align-middle text-right">{{ item.final_total }}</td>
                    </tr>
                  </tbody>
                  <tfoot>
                    <tr>
                      <td colspan="2" class="text-right"></td>
                      <!-- total -->
                      <td class="text-right">{{ order.total }}</td>
                    </tr>
                  </tfoot>
                </table>

                <table class="table">
                  <tbody>
                    <tr>
                      <th width="100">Email</th>
                      <!-- user -->
                      <td>{{ order.user.email }}</td>
                    </tr>
                    <tr>
                      <th></th>
                      <td>{{ order.user.name }}</td>
                    </tr>
                    <tr>
                      <th></th>
                      <td>{{ order.user.tel }}</td>
                    </tr>
                    <tr>
                      <th></th>
                      <td>{{ order.user.address }}</td>
                    </tr>
                    <tr>
                      <th></th>
                      <td>
                        <span v-if="!order.is_paid"></span>
                        <span v-else class="text-success"></span>
                      </td>
                    </tr>
                  </tbody>
                </table>
                <div class="text-right" v-if="order.is_paid === false">
                  <button class="btn btn-danger"></button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import ClientNavbar from "../ClientNavbar";
import Alert from "../AlertMessage";
import { mapActions, mapGetters } from "vuex";
export default {
  components: {
    ClientNavbar,
    Alert
  },
  data() {
    return {
      coupon_code: "", //
      //
      form: {
        user: {
          name: "",
          email: "",
          tel: "",
          address: ""
        },
        message: ""
      }
    };
  },
  computed: {
    ...mapGetters(["isLoading"]),
    ...mapGetters("cartModule", ["cart", "status", "orderId", "order"])
  },
  methods: {
    //(id)
    removeCartItem(id) {
      const vm = this;
      vm.$store.dispatch("cartModule/removeCartItem", id);
    },

    //codeAPI
    addCouponCode() {
      const vm = this;
      //API
      const coupon = {
        code: vm.coupon_code
      };
      vm.$store.dispatch("cartModule/addCouponCode", coupon);
    },

    //
    createOrder() {
      const vm = this;
      vm.$store.dispatch("cartModule/createOrder", vm.form);
    },

    //postapi
    payOrder() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/pay/${vm.orderId}`;
      vm.$store.dispatch("updateLoading", true); //vuexactions
      this.$http.post(api).then(response => {
        vm.$store.dispatch("updateLoading", false); //vuexactions
        if (response.data.success) {
          vm.$router.push(`/success`);
          vm.$store.dispatch("cartModule/clearOrderData");
        } else {
          vm.$store.dispatch("updateAlertMessage", {
            message: response.data.message,
            status: "danger"
          });
        }
      });
    },

    //changeStatus
    changeStatus(status) {
      const vm = this;
      vm.$store.dispatch("cartModule/changeStatus", status);
      vm.goCheckStatus();
    },

    goCheckStatus() {
      const vm = this;
      const status = vm.status;
      if (status == "cart") {
        $("#collapseCart").collapse("show");
      } else {
        $("#collapseCart").collapse("hide");
      }
    },

    //
    goBack() {
      this.$router.back();
    }
  },

  created() {
    this.$store.dispatch("cartModule/getCart");
  }
};
</script>

<style lang="scss" scoped>
.order-status {
  background-color: #fff;
  border: 1px solid var(--primary);
  border-radius: 5px;
  &.active {
    background-color: var(--primary);
    border: none;
    color: #fff;
    font-weight: bold;
  }
}
</style>


