<template>
  <div>
    <!-- loadingvue-loading-overlayisLoadingdata -->
    <loading :active.sync="isLoading"></loading>
    <div class="row mt-4">
      <!-- bs4(row)dataproducts -->
      <div class="col-md-4 mb-4" v-for="item in products" :key="item.id">
        <div class="card border-0 shadow-sm">
          <!-- stylestylebackground-imageES6 -->
          <div
            style="height: 150px; background-size: cover; background-position: center"
            :style="{backgroundImage:`url(${item.imageUrl})`}"
          ></div>
          <div class="card-body">
            <span class="badge badge-secondary float-right ml-2">{{item.category}}</span>
            <h5 class="card-title">
              <a href="#" class="text-dark">{{item.title}}</a>
            </h5>
            <p class="card-text">{{item.content}}</p>
            <div class="d-flex justify-content-between align-items-baseline">
              <!-- item.price -->
              <div class="h5" v-if="!item.price"> {{item.origin_price}} </div>
              <del class="h6" v-if="item.price"> {{item.origin_price}} </del>
              <div class="h5" v-if="item.price"> {{item.price}} </div>
            </div>
          </div>
          <div class="card-footer d-flex">
            <!-- getProductid -->
            <button
              type="button"
              class="btn btn-outline-secondary btn-sm"
              @click="getProduct(item.id)"
            >
              <!-- v-ifloadingstatus.loadingItemid -->
              <i class="fas fa-spinner fa-spin" v-if="status.loadingItem===item.id"></i>
              
            </button>
            <!-- addCartidfunction1 -->
            <button
              type="button"
              class="btn btn-outline-danger btn-sm ml-auto"
              @click="addtoCart(item.id)"
            >
              <i class="fas fa-spinner fa-spin" v-if="status.loadingItem===item.id"></i>
              
            </button>
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="row justify-content-center my5" v-if="cart.carts && cart.carts.length">
      <table class="table col-8">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <!-- datacartcarts -->
          <tr v-for="item in cart.carts" :v-if="cart.carts" :key="item.id">
            <td class="align-middle">
              <!-- removeCartItemid -->
              <button class="btn btn-outline-danger btn-sm" @click="removeCartItem(item.id)">
                <i class="far fa-trash-alt"></i>
              </button>
            </td>
            <td class="align-middle">
              {{item.product.title}}
              <div class="text-success" v-if="item.coupon"></div>
            </td>
            <td class="align-middle">{{item.qty}}/{{item.product.unit}}</td>
            <td class="align-middle text-right">{{item.final_total}}</td>
          </tr>
        </tbody>
        <tfoot>
          <tr>
            <td colspan="3" class="text-right"></td>
            <!-- datacarttotal -->
            <td class="text-right">{{cart.total}}</td>
          </tr>
          <tr v-if="cart.final_total!==cart.total">
            <td colspan="3" class="text-right text-success"></td>
            <td class="text-right">{{cart.final_total}}</td>
          </tr>
        </tfoot>
      </table>
      <div class="input-group mb-3 input-group-sm col-8">
        <!-- inputdatacoupon_code -->
        <input type="text" class="form-control" placeholder="" v-model="coupon_code" />
        <div class="input-group-append">
          <!-- addCouponCode -->
          <button class="btn btn-outline-secondary" type="button" @click="addCouponCode()"></button>
        </div>
      </div>
    </div>
    <!-- dataAPIusermessageformdatav-model -->
    <div class="my-5 row justify-content-center">
      <!-- VeeValidate3.xhandleSubmitValidationObserver v-slot="{handleSubmit}"
      form@submit.prevent="createOrder"="handleSubmit(createOrder)"createOrderfunction-->
      <ValidationObserver v-slot="{handleSubmit}" class="col-md-6">
        <!-- formsubmitcreateOrdersubmit -->
        <form @submit.prevent="handleSubmit(createOrder)">
          <div class="form-group">
            <label for="useremail">Email</label>
            <!-- v-validateinputrequiredemailemailerrors.has('name')true -->
            <!-- <input
            type="email"
            class="form-control"
            name="email"
            id="useremail"
            v-model="form.user.email"
            placeholder=" Email"
            v-validate="'required|email'"
            :class="{'is-invalid':errors.has('email')}"
            />-->
            <!-- v-validateinputnameerrors.has('name')true
            emailerrors.first('name')i18n-->
            <!-- <span class="text-danger" v-if="errors.has('email')">{{errors.first('email')}}</span> -->
            <ValidationProvider name="Email" rules="required|email" v-slot="{errors}">
              <input
                type="text"
                class="form-control"
                name="email"
                id="useremail"
                v-model="form.user.email"
                placeholder=" Email"
              />
              <span class="text-danger">{{errors[0]}}</span>
            </ValidationProvider>
          </div>
          <div class="form-group">
            <label for="username"></label>
            <!-- <input
            type="text"
            class="form-control"
            name="name"
            id="username"
            v-model="form.user.name"
            placeholder=""
            v-validate="'required'"
            :class="{'is-invalid':errors.has('name')}"
          />
            <span class="text-danger" v-if="errors.has('name')"></span>-->
            <ValidationProvider name="" rules="required" v-slot="{errors}">
              <input
                type="text"
                class="form-control"
                name="name"
                id="username"
                v-model="form.user.name"
                placeholder=""
              />
              <span class="text-danger">{{errors[0]}}</span>
            </ValidationProvider>
          </div>

          <div class="form-group">
            <label for="usertel"></label>
            <input
              type="tel"
              class="form-control"
              id="usertel"
              v-model="form.user.tel"
              placeholder=""
            />
          </div>

          <div class="form-group">
            <label for="useraddress"></label>
            <!-- <input
            type="text"
            class="form-control"
            name="address"
            id="useraddress"
            v-model="form.user.address"
            placeholder=""
            v-validate="'required'"
            :class="{'is-invalid':errors.has('address')}"
          />
            <span class="text-danger" v-if="errors.has('address')"></span>-->
            <ValidationProvider name="" rules="required" v-slot="{errors}">
              <input
                type="text"
                class="form-control"
                name="address"
                id="useraddress"
                v-model="form.user.address"
                placeholder=""
              />
              <span class="text-danger">{{errors[0]}}</span>
            </ValidationProvider>
          </div>

          <div class="form-group">
            <label for="comment"></label>
            <textarea
              name
              id="comment"
              class="form-control"
              cols="30"
              rows="10"
              v-model="form.message"
            ></textarea>
          </div>
          <div class="text-right">
            <button class="btn btn-danger"></button>
          </div>
        </form>
      </ValidationObserver>
    </div>
    <!-- Modal -->
    <div
      class="modal fade"
      id="productModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="modalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <!-- product -->
            <h5 class="modal-title" id="modalLabel">{{ product.title }}</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <img :src="product.imageUrl" class="img-fluid" alt />
            <blockquote class="blockquote mt-3">
              <p class="mb-0">{{ product.content }}</p>
              <footer class="blockquote-footer text-right">{{ product.description }}</footer>
            </blockquote>
            <div class="d-flex justify-content-between align-items-baseline">
              <div class="h4" v-if="!product.price"> {{ product.origin_price }} </div>
              <del class="h6" v-if="product.price"> {{ product.origin_price }} </del>
              <div class="h4" v-if="product.price"> {{ product.price }} </div>
            </div>
            <!-- v-modelproduct.numselect -->
            <select name class="form-control mt-3" v-model="product.num">
              <!-- v-for1-10 -->
              <option :value="num" v-for="num in 10" :key="num"> {{num}} {{product.unit}}</option>
            </select>
          </div>
          <div class="modal-footer">
            <div class="text-muted text-nowrap mr-3">
              
              <strong>{{ product.num * product.price }}</strong> 
            </div>
            <!-- addCartiddataidproduct.numvaluenum -->
            <button
              type="button"
              class="btn btn-primary"
              @click="addtoCart(product.id, product.num)"
            >
              <i class="fas fa-spinner fa-spin" v-if="product.id === status.loadingItem"></i>
              
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- Modal -->
  </div>
</template>

<script>
//modaljquery$
import $ from "jquery";

export default {
  data() {
    return {
      //data
      products: [],
      //data
      product: {},
      //
      cart: {},
      //coupon_code
      coupon_code: "",
      //
      form: {
        user: {
          name: "",
          email: "",
          tel: "",
          address: ""
        },
        message: ""
      },
      //id
      status: {
        loadingItem: "" //item.idv-ifid
      },
      //vue-loading-overlayisLoadingfalsetrue
      isLoding: false
    };
  },
  methods: {
    //getProductsAPI
    getProducts() {
      //api
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products`;
      //datavm=thisthis
      const vm = this;
      //vue-loading-overlayisLoading=true
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        //console.log
        console.log(response.data);
        //vue-loading-overlay
        vm.isLoading = false;
        //productsdataproducts
        vm.products = response.data.products;
        // //APIpaginationdatapagination
        // vm.pagination = response.data.pagination;
      });
    },
    //APIidid
    getProduct(id, num = 1) {
      //apiid
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`;
      //datavm=thisthis
      const vm = this;
      //idstatus.loadingItemv-ifloading
      vm.status.loadingItem = id;
      this.$http.get(api).then(response => {
        //console.log
        console.log(response.data);
        //productdataproduct
        vm.product = response.data.product;
        vm.$set(vm.product, "num", num);
        //BS4methodjQueryModal
        $("#productModal").modal("show");

        //Modalstatus.loadingItemloading
        vm.status.loadingItem = "";

        // //APIpaginationdatapagination
        // vm.pagination = response.data.pagination;
      });
    },
    //APIidqtyqty11
    addtoCart(id, qty = 1) {
      const vm = this;
      vm.status.loadingItem = id;
      //apiid
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      //{data:{'product_id':id,'qty':}}
      const cart = {
        product_id: id,
        qty
      };
      //APIpost
      this.$http.post(api, { data: cart }).then(response => {
        //console.log
        console.log(response.data);
        //status.loadingItemloading
        vm.status.loadingItem = "";
        //Modal
        $("#productModal").modal("hide");
        //function
        // //evenbusAlertMessageAlert
        // vm.$bus.$emit("message:push", response.data.message, "success");
        vm.$store.dispatch("updateAlertMessage", {
          message: response.data.message,
          status: "success"
        });
        //
        vm.getCart();
      });
    },
    //
    getCart() {
      //apiid
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      //datavm=thisthis
      const vm = this;
      vm.isLoading = true; //vue-loading-overlay
      this.$http.get(api).then(response => {
        //console.log
        console.log(response.data);
        vm.cart = response.data.data; //datacart
        vm.isLoading = false; //vue-loading-overlay
      });
    },
    //(id)
    removeCartItem(id) {
      //apiid
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
      //datavm=thisthis
      const vm = this;
      vm.isLoading = true; //vue-loading-overlay
      this.$http.delete(api).then(response => {
        //console.log
        console.log(response.data);
        //
        vm.getCart();
        // //evenbusAlertMessageAlert
        // vm.$bus.$emit("message:push", response.data.message, "success");
        vm.$store.dispatch("updateAlertMessage", {
          message: response.data.message,
          status: "success"
        });
      });
    },
    //codeAPI
    addCouponCode() {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/coupon`;
      const vm = this;
      //API
      const coupon = {
        code: vm.coupon_code
      };
      vm.isLoading = true; //vue-loading-overlay
      this.$http.post(api, { data: coupon }).then(response => {
        //console.log
        console.log(response.data);
        if (response.data.success) {
          // //evenbusAlertMessageAlert
          // vm.$bus.$emit("message:push", response.data.message, "success");
          vm.$store.dispatch("updateAlertMessage", {
            message: response.data.message,
            status: "success"
          });
        } else {
          // //evenbusAlertMessageAlert
          // vm.$bus.$emit("message:push", response.data.message, "danger");
          vm.$store.dispatch("updateAlertMessage", {
            message: response.data.message,
            status: "danger"
          });
        }
        //(APIcart)
        vm.getCart();
      });
    },
    //
    createOrder() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
      //order
      const order = vm.form;
      // //VeeValidateresulttruefalse
      // this.$validator.validate().then(result => {
      //   //API post
      //   if (result) {
      //     this.$http.post(api, { data: order }).then(response => {
      //       //console.log
      //       console.log("", response.data.message);
      //       //(APIcart)
      //       vm.getCart();
      //     });
      //   } else {
      //     //
      //     console.log('')
      //   }
      // });
      //vee-validate3.xValidationObserversubmitfunction
      this.$http.post(api, { data: order }).then(response => {
        //console.log
        console.log("", response.data.message);
        //(APIcart)
        vm.getCart();
        //CheckoutSimOrderaxiosorderId
        this.$router.push(`/checkout_sim_order/${response.data.orderId}`);
        // //evenbusAlertMessageAlert
        // vm.$bus.$emit("message:push", response.data.message, "success");
        vm.$store.dispatch("updateAlertMessage", {
          message: response.data.message,
          status: "success"
        });
      });
    }
  },
  created() {
    //
    this.getProducts();
    //
    this.getCart();
  }
};
</script>


