<template>
  <div>
    <!-- loadingvue-loading-overlayisLoadingdata -->
    <loading :active.sync="isLoading"></loading>

    <ClientNavbar></ClientNavbar>
    <Alert></Alert>
    <div class="container mt-4">
      <div class="row">
        <div class="col">
          <button class="btn btn-light" @click="goBack">
            <i class="fas fa-2x fa-reply"></i>
          </button>
        </div>
      </div>
      <div class="row">
        <div class="col-md-6 product p-3">
          <div class="row">
            <div class="col">
              <h1 class="title">{{product.title}}</h1>
            </div>
          </div>
          <div class="row mb-3">
            <div class="col">
              <span class="badge badge-info category">{{product.category}}</span>
            </div>
          </div>
          <div class="row">
            <div class="col">
              <span></span>
            </div>
          </div>
          <div class="row mb-5">
            <div class="col">
              <span class="description">{{product.description}}</span>
            </div>
          </div>
          <div class="row justify-content-around">
            <div class="col-5 price_title">
              <span></span>
            </div>
            <div class="col-5 price_title">
              <span></span>
            </div>
          </div>
          <div class="row mb-3 justify-content-around">
            <div class="col-5 d-flex align-items-center">
              <del class="origin_price">{{product.origin_price|currency}}</del>
            </div>
            <div class="col-5 d-flex align-items-center">
              <span class="price">{{product.price|currency}}</span>
            </div>
          </div>
          <div class="row">
            <div class="col-3 d-flex align-items-center">
              <span></span>
            </div>
            <div class="col-9">
              <!-- v-modelproduct.numselect -->
              <select name class="form-control form-control-sm" v-model="product.num">
                <!-- v-for1-10 -->
                <option :value="num" v-for="num in 10" :key="num">{{num}} {{product.unit}}</option>
              </select>
            </div>
          </div>
          <div class="row">
            <div class="col">
              <button
                class="btn btn-primary btn-block mt-3"
                @click="addtoCart(product.id,product.num)"
              >
                <i class="fas fa-spinner fa-spin" v-if="isLoading"></i>
              </button>
            </div>
          </div>
        </div>
        <div class="col-md-6 product p-3">
          <img class="img-fluid product_img" :src="product.imageUrl" />
          <div class="product_content p-3">
            <span></span>
            <span>{{product.content}}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ClientNavbar from "../ClientNavbar";
import Alert from "../AlertMessage";
import { mapActions, mapGetters } from "vuex";

export default {
  //
  components: {
    ClientNavbar,
    Alert
  },
  data() {
    return {
      productId: "", //id
      product: {} //
    };
  },
  computed: {
    ...mapGetters(["isLoading"]),
  },
  methods: {
    //APIididnum1
    getProduct(id, num = 1) {
      const vm = this;
      //apiid
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`;
      // vm.isLoading = true;
      vm.$store.dispatch("updateLoading", true); //vuexactions
      this.$http.get(api).then(response => {
        //console.log
        // console.log(response.data);
        //productdataproduct
        vm.product = response.data.product;
        vm.$set(vm.product, "num", num); //1
        // vm.isLoading = false;
        vm.$store.dispatch("updateLoading", false); //vuexactions
        // console.log(vm.product);
      });
    },
    //APIidqtyqty11
    addtoCart(id, qty = 1) {
      const vm = this;
      const cartItem = {
        product_id: id,
        qty
      };
      vm.$store.dispatch('cartModule/addtoCart',cartItem)
      vm.goBack();
    },
    goBack() {
      this.$router.back();
    }
  },
  created() {
    //createdthis.$route.params.productId:productIdproduct/
    const vm = this;
    vm.productId = vm.$route.params.productId;
    vm.getProduct(vm.productId);
  }
};
</script>

<style lang="scss" scoped>
.product {
  .title {
    color: var(--primary);
  }
  .category {
    font-size: 16px;
  }
  .description {
    font-size: 20px;
  }
  .origin_price {
    color: var(--danger);
  }
  .price {
    font-size: 32px;
    font-weight: bold;
  }
  .price_title {
    border-bottom: 2px solid var(--danger);
  }
  .product_img {
    border-radius: 5px 5px 0 0;
  }
  .product_content {
    background-color: var(--light);
    border-radius: 0 0 5px 5px;
  }
}
</style>


