<template>
  <div>
    <!-- <Loading :active.sync="isLoading"></Loading> -->
    <table class="table mt-4">
      <thead>
        <tr>
          <th width="100"></th>
          <th width="180">Email</th>
          <th></th>
          <th width="100"></th>
          <th width="80"></th>
          <th width="80"></th>
        </tr>
      </thead>
      <!-- ordertbody -->
      <tbody v-if="orders && orders.length">
        <!-- computedis_paidfalsetext-sacondaryclass -->
        <tr v-for="(item, key) in sortOrder" :key="key" :class="{'text-info': !item.is_paid}">
          <!-- datefilter -->
          <td>{{ item.create_at | date }}</td>
          <td>
            <!-- useremail -->
            <span v-text="item.user.email" v-if="item.user"></span>
          </td>
          <td>
            <ul class="list-unstyled">
              <!-- v-forproduct -->
              <li v-for="(product, i) in item.products" :key="i">
                {{ product.product.title }} {{ product.qty }}
                {{ product.product.unit }}
              </li>
            </ul>
          </td>
          <td class="text-right">{{ item.total | currency }}</td>
          <td>
            <strong v-if="item.is_paid" class="text-success"></strong>
            <span v-else class="text-muted"></span>
          </td>
          <td>
            <button class="btn btn-outline-primary btn-sm" @click="openModal(item)"></button>
          </td>
        </tr>
      </tbody>
    </table>
    <!-- paginationpaginationpropspages
    functionemitPagesgetOrders-->
    <Pagination :pages="pagination" @emitPages="getOrders"></Pagination>

    <!-- Modal -->
    <div
      class="modal fade"
      id="orderModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="modalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-primary text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div>{{tempOrder.create_at|date}}</div>
            <div>
              
              <span v-if="tempOrder.is_paid" class="text-success"></span>
              <span v-else class="text-danger"></span>
            </div>
            <div class="row mt-3">
              <div class="col-6">
                <div></div>
                <hr />
                <div class="form-group">
                  <label for></label>
                  <input type="text" v-model="tempOrder.user.name" class="form-control" />
                </div>
                <div class="form-group">
                  <label for>email</label>
                  <input type="email" v-model="tempOrder.user.email" class="form-control" />
                </div>
                <div class="form-group">
                  <label for></label>
                  <input type="tel" v-model="tempOrder.user.tel" class="form-control" />
                </div>
                <div class="form-group">
                  <label for></label>
                  <input type="text" v-model="tempOrder.user.address" class="form-control" />
                </div>
                <div></div>
                <div>{{tempOrder.message}}</div>
              </div>
              <div class="col-6">
                <div></div>
                <hr />
                <div
                  v-for="item in tempOrder.products"
                  :key="item.id"
                  class="card border-success mb-2"
                >
                  <div class="card-body form-group">
                    <div
                      class="card-title"
                      style="font-weight:bold;font-size:20px;"
                    >{{item.product.title}}</div>
                    <label for></label>
                    <select name class="form-control" v-model="item.qty">
                      <!-- v-for1-10 -->
                      <option
                        :value="num"
                        v-for="num in 10"
                        :key="num"
                      >{{num}} {{item.product.unit}}</option>
                    </select>
                  </div>
                  <div class="card-footer text-right bg-transparent">
                    <button
                      class="btn btn-outline-danger btn-sm"
                      @click="delOrderItem(item.id)"
                    ></button>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <!-- v-onmethods -->
            <button type="button" class="btn btn-primary" @click="editOrder"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import jQuery from "jquery";
//
import Pagination from "../Pagination";

export default {
  //Pagination
  components: {
    Pagination
  },
  data() {
    return {
      //vue-loading-overlayisLoadingfalsemethod
      // isLoading: false,
      //orders
      orders: [],
      //
      pagination: {},
      //
      tempOrder: {
        products: [],
        user: {}
      }
    };
  },
  methods: {
    getOrders(page = 1) {
      //axiospage1pagination
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/orders?page=${page}`;
      // vm.isLoading = true; //loading
      vm.$store.dispatch('updateLoading',true)
      this.$http.get(api).then(response => {
        console.log(response.data);
        vm.orders = response.data.orders; //orders
        vm.pagination = response.data.pagination; //paginationpagination
        // vm.isLoading = false; //loading
        vm.$store.dispatch('updateLoading',false)
      });
    },
    //Modal
    openModal(item) {
      const vm = this;
      //Object.assigndeletorders
      //vm.tempOrder = Object.assign({}, item);
      //jQuery.extendimport jQuerytrue
      vm.tempOrder = jQuery.extend(true, {}, item);
      $("#orderModal").modal("show");
    },
    //
    delOrderItem(itemId) {
      const vm = this;
      vm.$delete(vm.tempOrder.products, itemId);
    },
    //
    editOrder() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/order/${vm.tempOrder.id}`;
      // vm.isLoading = true;
      vm.$store.dispatch('updateLoading',true)
      //put
      this.$http.put(api, { data: vm.tempOrder }).then(response => {
        console.log(response.data.message);
        if (response.data.success) {
          //(axiosapi)
          vm.getOrders();
          // vm.isLoading = false;
          vm.$store.dispatch('updateLoading',false)
          //Modal
          $("#orderModal").modal("hide");
          // //evenbusAlertMessageAlert
          // vm.$bus.$emit("message:push", response.data.message, "success");
          vm.$store.dispatch("updateAlertMessage", {
            message: response.data.message,
            status: "success"
          });
        } else {
          // //evenbusAlertMessageAlert
          // vm.$bus.$emit("message:push", response.data.message, "danger");
          vm.$store.dispatch("updateAlertMessage", {
            message: response.data.message,
            status: "danger"
          });
        }
      });
    }
  },
  computed: {
    //->
    sortOrder() {
      const vm = this;
      let newOrder = []; //return
      if (vm.orders.length) {
        //>0
        newOrder = vm.orders.sort((a, b) => {
          //newOrderorderssort
          const aIsPaid = a.is_paid ? 1 : 0; //truefalse10
          const bIsPaid = b.is_paid ? 1 : 0;
          return bIsPaid - a.is_paid; //()
        });
      }
      return newOrder;
    }
  },
  created() {
    this.getOrders();
  }
};
</script>


