<template>
  <div>
    <!-- loadingvue-loading-overlayisLoadingdata
    <loading :active.sync="isLoading"></loading> -->
    <div class="text-right mt-4">
      <!-- bootstrapmodaldata-toggle="modal" data-target="#ModalID"
      UsageMethodsvueVueMethods-->
      <!-- truefunctionisNewtrue -->
      <button class="btn btn-primary" @click="openModal(true)"></button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th width="120"></th>
          <th></th>
          <th width="80"></th>
          <th width="80"></th>
          <th width="100"></th>
          <th width="120"></th>
        </tr>
      </thead>
      <tbody>
        <!-- v-fordataproductstrtreslintkeyid -->
        <tr v-for="(item) in products" :key="item.id">
          <td>{{item.category}}</td>
          <td>{{item.title}}</td>
          <!-- filtersrc/filters/currencyFiltermain.jscurrencycurrency -->
          <td class="text-right">{{item.origin_price|currency}}</td>
          <td class="text-right">{{item.price|currency}}</td>
          <td>
            <span v-if="item.is_enabled" class="text-success"></span>
            <span v-else class="text-secondary"></span>
          </td>
          <td>
            <!-- ModalfalseitemfunctionisNew=falseitem -->
            <button class="btn btn-outline-primary btn-sm" @click="openModal(false,item)"></button>
            <button class="btn btn-outline-danger btn-sm" @click="delProductModal(item)"></button>
          </td>
        </tr>
      </tbody>
    </table>
    <!-- PaginationpropspagesgetProductspagination
    emitPagesgetProductsfunctionpage-->
    <Pagination :pages="pagination" @emitPages="getProducts" class="mt-5"></Pagination>

    <!-- Modal -->
    <div
      class="modal fade"
      id="productModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-primary text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span v-if="isNew"></span>
              <span v-else></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group">
                  <label for="image"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="image"
                    placeholder=""
                    v-model="tempProduct.imageUrl"
                  />
                </div>
                <div class="form-group">
                  <label for="customFile">
                     
                    <!-- fontawesomeloadingtrue -->
                    <i class="fas fa-spinner fa-spin" v-if="status.fileUploading"></i>
                  </label>
                  <!-- inputtypefilechange -->
                  <input
                    type="file"
                    id="customFile"
                    class="form-control"
                    ref="files"
                    @change="uploadFile"
                  />
                </div>
                <img
                  img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                  class="img-fluid"
                  :src="tempProduct.imageUrl"
                  alt
                />
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="title"
                    placeholder=""
                    v-model="tempProduct.title"
                  />
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category"></label>
                    <input
                      type="text"
                      class="form-control"
                      id="category"
                      placeholder=""
                      v-model="tempProduct.category"
                    />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      type="unit"
                      class="form-control"
                      id="unit"
                      placeholder=""
                      v-model="tempProduct.unit"
                    />
                  </div>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="origin_price"></label>
                    <input
                      type="number"
                      class="form-control"
                      id="origin_price"
                      placeholder=""
                      v-model="tempProduct.origin_price"
                    />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      type="number"
                      class="form-control"
                      id="price"
                      placeholder=""
                      v-model="tempProduct.price"
                    />
                  </div>
                </div>
                <hr />

                <div class="form-group">
                  <label for="description"></label>
                  <textarea
                    type="text"
                    class="form-control"
                    id="description"
                    placeholder=""
                    v-model="tempProduct.description"
                  ></textarea>
                </div>
                <div class="form-group">
                  <label for="content"></label>
                  <textarea
                    type="text"
                    class="form-control"
                    id="content"
                    placeholder=""
                    v-model="tempProduct.content"
                  ></textarea>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <!-- checkboxcheckboxtruefalsetrue-value1false-value0 -->
                    <input
                      class="form-check-input"
                      type="checkbox"
                      id="is_enabled"
                      v-model="tempProduct.is_enabled"
                      :true-value="1"
                      :false-value="0"
                    />
                    <label class="form-check-label" for="is_enabled"></label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <!-- v-onmethods -->
            <button type="button" class="btn btn-primary" @click="updateProduct"></button>
          </div>
        </div>
      </div>
    </div>
    <!-- Modal for  -->
    <div
      class="modal fade"
      id="delProductModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            
            <strong class="text-danger">{{ tempProduct.title }}</strong> ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger" @click="delProduct"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
//methodsjquery$$import
import $ from "jquery";
//Pagination
import Pagination from "../Pagination";

export default {
  data() {
    return {
      //productsaxios
      products: [],
      //Modal
      tempProduct: {},
      //isNewtruefalsefalse
      isNew: false,
      //status
      status: {
        //falsehtmlv-iftrue
        //methodtrueajaxfalse
        fileUploading: false
      },
      //paginationgetProductsaxiospagination(total_pagescurrent_pagehas_prehas_next)
      pagination: {}
    };
  },
  methods: {
    //admin/products?page=:page:pagepage1
    getProducts(page = 1) {
      //apipagefunctionpage=1
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/products?page=${page}`;
      //datavm=thisthis
      const vm = this;

      //vue-loading-overlayisLoading=true
      // vm.isLoading = true;
      vm.$store.dispatch('updateLoading',true)

      this.$http.get(api).then(response => {
        //console.log
        console.log(response.data);

        //vue-loading-overlay
        // vm.isLoading = false;
        vm.$store.dispatch('updateLoading',false)

        //productsdataproducts
        vm.products = response.data.products;

        //APIpaginationdatapagination
        vm.pagination = response.data.pagination;
      });
    },
    //openModalfunction
    openModal(isNew, item) {
      //isNewtruefalse
      if (isNew) {
        //tempProductisNewupdateProduct
        this.tempProduct = {};
        this.isNew = true;
      } else {
        //tempProductitemtempProduct=item
        //ES6Object.assign()itemtempProduct
        this.tempProduct = Object.assign({}, item);
        this.isNew = false;
      }
      //Modal

      //bs4methodmodaljqueryjquerypopper.js
      //scriptimport$ from'jquery'$$jquery
      $("#productModal").modal("show");
    },
    updateProduct() {
      //function
      //apilet
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product`;
      const vm = this;
      //APIpostputpost)
      let httpMethod = "post";
      //isNewisNew==falseapihttpMethodisNewvm
      if (!vm.isNew) {
        //apiidtempProductvmid
        api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
        //httpMethodput
        httpMethod = "put";
      }

      //APIdata{data:vm.}(tempProduct)
      //axiosposttempProduct
      //puttempProduct
      //httpMethod[]
      this.$http[httpMethod](api, { data: vm.tempProduct }).then(response => {
        //console.log
        console.log(response.data);
        //
        if (response.data.success) {
          //Modal
          $("#productModal").modal("hide");
          // //evenbusAlertMessageAlert
          // vm.$bus.$emit("message:push", response.data.message, "success");
          vm.$store.dispatch("updateAlertMessage", {
            message: response.data.message,
            status: "success"
          });
          //
          vm.getProducts();
        } else {
          //
          //Modal
          $("#productModal").modal("hide");
          //
          vm.getProducts();
          //console.log
          console.log(response.data.message);
          // //eventbusAlertMessageAlert
          // this.$bus.$emit("message:push", response.data.message, "danger");
          vm.$store.dispatch("updateAlertMessage", {
            message: response.data.message,
            status: "danger"
          });
        }
      });
    },
    delProductModal(item) {
      //itemtempProductES6Object.assign()
      this.tempProduct = Object.assign({}, item);
      //Modal
      $("#delProductModal").modal("show");
    },
    delProduct() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
      this.$http.delete(api).then(response => {
        //console.log
        console.log(response.data);
        //Modalconsole.log
        if (response.data.success) {
          //Modal
          $("#delProductModal").modal("hide");
          // //evenbusAlertMessageAlert
          // vm.$bus.$emit("message:push", response.data.message, "success");
          vm.$store.dispatch("updateAlertMessage", {
            message: response.data.message,
            status: "success"
          });
          //
          vm.getProducts();
        } else {
          //Modal
          $("#delProductModal").modal("hide");
          //
          vm.getProducts();
          //console.log
          console.log(response.data.message);
          // //eventbusAlertMessageAlert
          // this.$bus.$emit("message:push", response.data.message, "danger");
          vm.$store.dispatch("updateAlertMessage", {
            message: response.data.message,
            status: "danger"
          });
        }
      });
    },

    //
    uploadFile() {
      //console.logthisVueComponent
      console.log(this);
      //this$refs.files.files0
      const uploadedFile = this.$refs.files.files[0];
      const vm = this;

      //vm.status.fileUploadingtrue)
      vm.status.fileUploading = true;

      //WebAPIFormData()formData
      const formData = new FormData();
      //formDataappend('API',)formData
      formData.append("file-to-upload", uploadedFile);
      //urlapi
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/upload`;
      //postapi,,{headerscontent-typeform-data})
      this.$http
        .post(url, formData, {
          headers: {
            "Content-Type": "multipart/form-data"
          }
        })
        .then(response => {
          //vm.status.fileUploadingfalse)
          vm.status.fileUploading = false;

          //success==trueFirebaseimageUrl
          console.log(response.data);
          //
          if (response.data.success) {
            //tempProductgetsetvm.$set(,(),)
            vm.$set(vm.tempProduct, "imageUrl", response.data.imageUrl);
            // //evenbusAlertMessageAlert
            // vm.$bus.$emit("message:push", response.data.message, "success");
            vm.$store.dispatch("updateAlertMessage", {
              message: response.data.message,
              status: "success"
            });
          } else {
            // //eventbusAlertMessageAlert
            // vm.$bus.$emit("message:push", response.data.message, "danger");
            vm.$store.dispatch("updateAlertMessage", {
              message: response.data.message,
              status: "danger"
            });
          }
        });
    }
  },
  created() {
    this.getProducts();
  },
  components: {
    Pagination
  }
};
</script>


