import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
//
import Shop from '@/components/Shop';
import Login from '@/components/pages/Login'
//Dashboard
import Dashboard from '@/components/Dashboard'
//DashboardProducts
import Products from '@/components/pages/Products'
//DashboardSimulationOrders.vue
import SimulationOrders from '@/components/pages/SimulationOrders'
//SimOrder
import CheckoutSimOrder from '@/components/pages/CheckoutSimOrder';
//DashboardOrdersCoupons
import Orders from '@/components/pages/Orders';
import Coupons from '@/components/pages/Coupons';

//
import Product from '@/components/pages/Product';
//
import Cart from '@/components/pages/Cart';
//
import Success from '@/components/pages/Success';

Vue.use(Router)

export default new Router({
  //=router-linkactiveclass
  linkActiveClass: 'active',
  //
  routes: [
    //
    {
      //path*path
      path: '*',
      //redirect
      redirect: '/login',
    },
    {
      path: '/',
      name: 'Shop',
      component: Shop,
    },
    {
      //idid(router-viewShop)
      path: '/product/:productId',
      name: 'Product',
      component: Product
    },
    {
      path: '/cart',
      name: 'Cart',
      component: Cart,
    },
    {
      path: '/success',
      name: 'Success',
      component: Success,
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,
    },
    {
      //Dashboard
      path: '/admin',
      name: 'Dashboard',
      component: Dashboard,
      //children
      children: [
        {
          path: 'products',
          name: 'Products',
          component: Products,
          //metametarequiresAuth=truemain.jsrouter.beforeEachrouter.beforeEach
          meta: {
            requiresAuth: true,
          },
        },
        {
          //OrdersCouponsadmin/meta
          path: 'orders',
          name: 'Orders',
          component: Orders,
          //metametarequiresAuth=truemain.jsrouter.beforeEachrouter.beforeEach
          meta: {
            requiresAuth: true,
          },
        },
        {
          path: 'coupons',
          name: 'Coupons',
          component: Coupons,
          //metametarequiresAuth=truemain.jsrouter.beforeEachrouter.beforeEach
          meta: {
            requiresAuth: true,
          },
        },
      ]
    },
    {
      //Dashboardproductssimulation_orders
      path: '/simulation',
      name: 'Simulation',
      component: Dashboard,
      //childrensimulation_orders/#/simulation_orders
      children: [
        {
          path: 'simulation_orders',
          name: 'SimulationOrders',
          component: SimulationOrders,
        },
        {
          //orderIdidCheckoutSimOrderthis.$route.params.orderIdid
          path: 'checkout_sim_order/:orderId',
          name: 'CheckoutSimOrderCh',
          component: CheckoutSimOrder,
        },
      ]
    },
  ]
})



